import React, { useEffect, useReducer } from "react";
import {
  Layout,
  Menu,
  Button,
  PageHeader,
  Spin,
  Collapse,
  Tag,
  Space,
} from "antd";
import { SettingOutlined } from "@ant-design/icons";
import "antd/dist/antd.css";
import "./App.css";
import DataSourceViewer from "./components/DataSourceViewer";
import ConfigEditor from "./components/SourceConfigEditor";
import { addSources, setSelectedSource, updateSource } from "./store/actions";
import { reducer } from "./store/reducers";
import { initialState, DataSource } from "./store/state";
import { httpRequest } from "./utils/requests";

const { Header, Footer, Sider, Content } = Layout;

const listPathsTemplate: string = `def main():
    """
    Return a list of paths identifying where the data is located.
    Typically for file paths, URLs, or database connection strings.
    """
    return []
`;

const getDataTemplate: string = `import pandas as pd


def main(path: str) -> pd.DataFrame:
    """
    Given a path, retrieve that data and return it as a pandas dataframe
    """
    raise NotImplementedError()

`;

const templateDataSource = (): DataSource => ({
  id: "",
  name: "",
  packageName: "",
  nodes: {},
  nodesFullyLoaded: false,
  visible: true,
  listPaths: listPathsTemplate,
  getData: getDataTemplate,
});

const App = () => {
  const [state, dispatch] = useReducer(reducer, initialState);

  useEffect(() => {
    httpRequest({
      method: "GET",
      url: "/source/list/",
      resolve: (sources) => dispatch(addSources(sources)),
      reject: (error) => null,
    });
  }, []);

  const loadSourceNodes = (source: DataSource) => {
    dispatch(updateSource({ ...source, loading: true }));
    httpRequest({
      method: "POST",
      url: "/source/nodes/list/",
      body: source,
      resolve: (data) => dispatch(updateSource({ ...data, loading: false })),
      reject: (error) => dispatch(updateSource({ ...source, loading: false })),
    });
  };

  useEffect(() => {
    if (state.sources !== undefined) {
      state.sources
        .filter(
          (source) =>
            !source.nodesFullyLoaded &&
            !source.loading &&
            Object.keys(source.nodes || {}).length === 0
        )
        .forEach((source) => {
          loadSourceNodes(source);
        });
    }
  }, [state.sources]);

  return (
    <Layout className="layout">
      <Header style={{ position: "fixed", zIndex: 1, width: "100%" }}>
        <Menu theme="dark" mode="horizontal" defaultSelectedKeys={["sources"]}>
          <Menu.Item key="sources">Data Sources</Menu.Item>
          <Menu.Item key="instances">D-Tale Instances</Menu.Item>
        </Menu>
      </Header>
      <Content style={{ width: "100vw", marginTop: 64 }}>
        <PageHeader
          title="Data Sources"
          className="site-page-header"
          subTitle="This is a subtitle"
          extra={[
            <Button
              key="newDataSource"
              type="primary"
              onClick={() => {
                dispatch(setSelectedSource(templateDataSource()));
              }}
            >
              Add Data Source
            </Button>,
          ]}
        />
        <div style={{ width: "100%" }}>
          {state.sources === undefined ? (
            <Spin />
          ) : (
            <Collapse defaultActiveKey={[]}>
              {state.sources.map((source) => (
                <Collapse.Panel
                  key={source.id}
                  disabled={source.loading}
                  header={
                    <Space>
                      <span>{source.name}</span>
                      <Tag>
                        {source.loading ? (
                          <Spin size="small" />
                        ) : (
                          `${Object.keys(source.nodes).length} results`
                        )}
                      </Tag>
                      {source.nodesFullyLoaded ? null : (
                        <Button
                          key={`${source.id}loadmore`}
                          size="small"
                          onClick={(event) => {
                            event.stopPropagation();
                            loadSourceNodes(source);
                          }}
                        >
                          Load more
                        </Button>
                      )}
                    </Space>
                  }
                  extra={[
                    <SettingOutlined
                      key={`${source.id}settings`}
                      onClick={(event) => {
                        event.stopPropagation();
                        dispatch(setSelectedSource(source));
                      }}
                    />,
                  ]}
                >
                  <DataSourceViewer dispatch={dispatch} source={source} />
                </Collapse.Panel>
              ))}
            </Collapse>
          )}
        </div>
        {state.selectedSource ? (
          <ConfigEditor source={state.selectedSource} dispatch={dispatch} />
        ) : null}
      </Content>
    </Layout>
  );
};

export default App;
