import React, { Dispatch, useEffect, useState } from "react";
import { DataSource, DataNode } from "../store/state";
import { Action, updateNode, updateSource } from "../store/actions";
import { List, Spin, Button, Alert } from "antd";
import { httpRequest } from "../utils/requests";
import { ViewTableButton, ViewChartsButton, ShutdownButton } from "./DataNodeActions";
import { LineChartOutlined, TableOutlined, PoweroffOutlined } from "@ant-design/icons";

type Props = {
  dispatch: Dispatch<Action>;
  source: DataSource;
};

const DataSourceViewer: React.FC<Props> = ({ dispatch, source }) => {
  const [loading, setLoading] = useState<boolean>(false);

  // useEffect(() => {
  //   httpRequest({
  //     method: "GET",
  //     url: "/node/list/",
  //     params: {source_id: source.id},
  //     resolve: (data: DataNode[]) => dispatch(addNodes(data)),
  //     reject: (error) => undefined,
  //     onStart: () => setLoading(true),
  //     onFinish: () => setLoading(false),
  //   });
  // }, []);

  const renderBody = () => {
    if (loading) {
      return <Spin />;
    } else {
      return (
        <List
          size="small"
          bordered
          dataSource={Object.values(source.nodes)}
          renderItem={(node) => (
            <List.Item
              actions={[
                <ViewTableButton dispatch={dispatch} node={node} />,
                <ViewChartsButton dispatch={dispatch} node={node} />,
                <ShutdownButton dispatch={dispatch} node={node} />,
              ]}
            >
              <List.Item.Meta
                title={node.path}
                description={
                  node.error ? (
                    <Alert type="error" message={node.error} closable />
                  ) : (
                    node.dtaleUrl || null
                  )
                }
              />
            </List.Item>
          )}
        />
      );
    }
  };

  return renderBody();
};

export default DataSourceViewer;
